.user-name,
.password,
.submit {
  margin-bottom: 20px;
}
.login {
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: #faebcc;
}
.login .login-form {
  width: 500px;
  height: 300px;
  margin: 100px auto 0;
  padding: 30px 20px;
  background-color: #fff;
  border: 1px solid #eee;
  box-shadow: 0 0 5px #888888;
  border-radius: 5px;
}
