.header {
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;
}
.header .menu {
  width: 90%;
  margin: 0 auto;
  line-height: 47px;
}
.header .menu .logo {
  height: 40px;
  margin-top: 4px;
}
.header .menu .search {
  margin-top: 12px;
  width: 98%;
}
.header .menu .header-user {
  text-align: right;
  margin-right: 10px;
}
.header .menu .header-user .header-user-name {
  margin: 0 10px;
  text-decoration: underline;
  color: #ff6a00;
}
.header .menu .header-user .header-user-login {
  margin-right: 10px;
}
.ant-menu-horizontal {
  border-bottom: 0;
}
.ant-dropdown-link {
  margin-left: 20px;
}
